.image-grid {
	display: flex; 
	flex-direction: column;
	align-items: center;
	justify-content: center;
	
}

#heading {
	margin: 10vh;
}

@media only screen and (min-width: 600px){

	.image-grid {
	display: flex; 
	flex-direction: row;
	align-items:center;
	 justify-content: space-around;
}


}